<template>
  <div>
    <h5 v-if="certificates.length === 0" class="text-center">
      Удостоверения не найдены
    </h5>
    <div v-else>
      <div class="certificates-list">
        <div class="row">
          <div class="certificates-list-nav">
            <p>Найдено записей: {{ certificates.length }}</p>
            <nav
              aria-label="Page navigation example"
              v-if="certificates.length > perPage || currentPage > 1"
            >
              <ul class="pagination" v-if="pages.length">
                <li class="page-item">
                  <button
                    class="page-link"
                    type="button"
                    aria-label="First"
                    @click="onClickFirstPage"
                    :disabled="isInFirstPage"
                  >
                    <span aria-hidden="true">&laquo;</span>
                  </button>
                </li>

                <li class="page-item">
                  <button
                    class="page-link"
                    type="button"
                    aria-label="Previous"
                    @click="onClickPreviousPage"
                    :disabled="isInFirstPage"
                  >
                    <span aria-hidden="true">&#8249;</span>
                  </button>
                </li>
                <li
                  :class="['page-item', { active: isPageActive(page.number) }]"
                  v-for="(page, index) in pages"
                  :key="index"
                >
                  <button
                    class="page-link"
                    type="button"
                    @click="onClickPage(page.number)"
                    :disabled="page.isDisabled"
                  >
                    {{ page.number }}
                  </button>
                </li>

                <li class="page-item">
                  <button
                    class="page-link"
                    type="button"
                    aria-label="Next"
                    @click="onClickNextPage"
                    :disabled="isInLastPage"
                  >
                    <span aria-hidden="true">&#8250;</span>
                  </button>
                </li>

                <li class="page-item">
                  <button
                    class="page-link"
                    type="button"
                    aria-label="Next"
                    @click="onClickLastPage"
                    :disabled="isInLastPage"
                  >
                    <span aria-hidden="true">&raquo;</span>
                  </button>
                </li>
              </ul>
            </nav>
          </div>

          <div class="certificates-content table-responsive">
            <table class="table table-sm table-bordered">
              <thead class="fs-7">
                <tr>
                  <!-- <th scope="col" class="text-center">#</th> -->
                  <!-- <th scope="col" class="text-center">Дата</th> -->
                  <th scope="col" class="text-center align-middle">
                    Номер удостоверения
                  </th>
                  <th scope="col" class="text-center align-middle">ФИО</th>
                  <th scope="col" class="text-center align-middle">
                    Клеймо
                  </th>
                  <th scope="col" class="text-center align-middle">
                    Способ сварки
                  </th>
                  <th scope="col" class="text-center align-middle">
                    Группа технических устройств
                  </th>
                  <th scope="col" class="text-center align-middle">
                    Срок аттестации
                  </th>
                  <!-- <th scope="col" class="text-center">
                    Аттестационная комиссия
                  </th>
                  <th scope="col" class="text-center">Номер удостоверения</th> -->
                  <th scope="col"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="cert in paginatedData" :key="cert.id">
                  <!-- <th scope="row" class="text-center">{{ +cert.id + 1 }}</th> -->
                  <!-- <td>{{ dateFormat(cert.date) }}</td> -->
                  <td>{{ cert.numCertificate }}</td>
                  <td>
                    {{
                      cert.firstname +
                        " " +
                        cert.secondname +
                        " " +
                        cert.lastname
                    }}
                  </td>
                  <td>{{ cert.stigmaGeneral }}</td>
                  <td>{{ cert.weldingMethodScope }}</td>
                  <td>
                    {{ arrayFormat(shortingTableItems(cert.accesses)) + "" }}
                  </td>
                  <td>{{ dateFormat(cert.expiration) }}</td>
                  <!-- <td>{{ arrayFormat(cert.comission) + "" }}</td> -->
                  <!-- <td>{{ cert.numCertificate }}</td> -->
                  <td class="text-center">
                    <router-link
                      v-slot="{ navigate }"
                      :to="{ name: 'Certificate', params: { id: cert._id } }"
                    >
                      <button
                        type="button"
                        @click="navigate"
                        class="btn btn-outline-primary btn-sm"
                      >
                        Открыть
                      </button>
                    </router-link>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>

          <div class="certificates-nav pt-2">
            <nav
              aria-label="Page navigation example"
              v-if="certificates.length > perPage || currentPage > 1"
            >
              <ul class="pagination" v-if="pages.length">
                <li class="page-item">
                  <button
                    class="page-link"
                    type="button"
                    aria-label="First"
                    @click="onClickFirstPage"
                    :disabled="isInFirstPage"
                  >
                    <span aria-hidden="true">&laquo;</span>
                  </button>
                </li>

                <li class="page-item">
                  <button
                    class="page-link"
                    type="button"
                    aria-label="Previous"
                    @click="onClickPreviousPage"
                    :disabled="isInFirstPage"
                  >
                    <span aria-hidden="true">&#8249;</span>
                  </button>
                </li>
                <li
                  :class="['page-item', { active: isPageActive(page.number) }]"
                  v-for="(page, index) in pages"
                  :key="index"
                >
                  <button
                    class="page-link"
                    type="button"
                    @click="onClickPage(page.number)"
                    :disabled="page.isDisabled"
                  >
                    {{ page.number }}
                  </button>
                </li>

                <li class="page-item">
                  <button
                    class="page-link"
                    type="button"
                    aria-label="Next"
                    @click="onClickNextPage"
                    :disabled="isInLastPage"
                  >
                    <span aria-hidden="true">&#8250;</span>
                  </button>
                </li>

                <li class="page-item">
                  <button
                    class="page-link"
                    type="button"
                    aria-label="Next"
                    @click="onClickLastPage"
                    :disabled="isInLastPage"
                  >
                    <span aria-hidden="true">&raquo;</span>
                  </button>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </div>
    <router-link
      to="/add-certificate"
      class="btn btn-success btn-add rounded-circle shadow"
    >
      <span></span>
    </router-link>
  </div>
</template>

<script>
import { computed, onMounted, ref, watch } from "vue";
import { dateFormat } from "../../utils/dateFormat";
import { shortingTableItems } from "../../utils/shortingTableItems";
import { arrayFormat } from "../../utils/arrayFormat";
import router from "../../router";

export default {
  props: ["certificates", "perPage"],
  setup(props) {
    //максимальное количество нуерных кнопок страниц
    const maxVisibleButtons = ref(5);
    //общее количество удостоверений
    // const total = ref(props.certificates.length);
    //количество на странице
    const perPage = ref(props.perPage);
    //текущая страница
    const currentPage = ref(1);
    //всего страниц
    const totalPages = ref(
      Math.ceil(props.certificates.length / props.perPage)
    );

    //проверка адресной строки браузера
    onMounted(() => {
      const href = router.currentRoute.value.href;
      const pageNum = +href.substr(12);

      if (
        href.substr(0, 12) === "/index?page=" &&
        !isNaN(pageNum) &&
        pageNum > 0 &&
        pageNum <= totalPages.value
      ) {
        currentPage.value = pageNum;
      } else if (pageNum === 0) {
        currentPage.value = 1;
        router.push("/");
      } else if (pageNum < 0) {
        currentPage.value = 1;
        router.push("/");
      } else if (pageNum > totalPages.value) {
        currentPage.value = totalPages.value;
        router.push(`/index?page=${currentPage.value}`);
      } else {
        router.push("/");
      }
    });

    const paginatedData = computed(() => {
      let start = (currentPage.value - 1) * perPage.value,
        end = start + perPage.value;

      return props.certificates.slice(start, end);
    });
    const startPage = computed(() => {
      if (currentPage.value === 1) return 1;
      if (currentPage.value === totalPages.value)
        return (
          totalPages.value -
          maxVisibleButtons.value +
          (maxVisibleButtons.value - 1)
        );
      return currentPage.value - 1;
    });
    const endPage = computed(() => {
      return Math.min(
        startPage.value + maxVisibleButtons.value - 1,
        totalPages.value
      );
    });
    const pages = computed(() => {
      let range = [];
      if (props.certificates.length && endPage.value) {
        for (let i = startPage.value; i <= endPage.value; i++) {
          range.push({
            number: i,
            isDisabled: i === currentPage.value,
          });
        }
      }
      return range;
    });
    const isInFirstPage = computed(() => {
      return currentPage.value === 1;
    });
    const isInLastPage = computed(() => {
      return currentPage.value === totalPages.value;
    });

    const onClickFirstPage = () => {
      currentPage.value = 1;
      router.push("/index?page=1");
    };
    const onClickPreviousPage = () => {
      currentPage.value = currentPage.value - 1;
      router.push(`/index?page=${currentPage.value}`);
    };
    const onClickPage = (page) => {
      currentPage.value = page;

      // /home?page=1
      router.push(`/index?page=${page}`);
    };
    const onClickNextPage = () => {
      currentPage.value = currentPage.value + 1;
      router.push(`/index?page=${currentPage.value}`);
    };
    const onClickLastPage = () => {
      currentPage.value = totalPages.value;
      router.push(`/index?page=${currentPage.value}`);
    };
    const isPageActive = (page) => {
      return currentPage.value === page;
    };
    const onPageChange = (page) => {
      currentPage.value = page;
    };

    //правльность отображения списка при фильтрации, если показана текущая страница != 1
    watch(
      () => props.certificates,
      (certificatesList) => {
        if (certificatesList.length < 11 && currentPage.value > 1) {
          currentPage.value = 1;
        }
      }
    );

    return {
      dateFormat,
      shortingTableItems,
      arrayFormat,

      maxVisibleButtons,
      // total,
      perPage,
      currentPage,
      totalPages,

      paginatedData,
      startPage,
      endPage,
      pages,
      isInFirstPage,
      isInLastPage,

      onClickFirstPage,
      onClickPreviousPage,
      onClickPage,
      onClickNextPage,
      onClickLastPage,
      isPageActive,
      onPageChange,
    };
  },

  components: {},
};
</script>

<style></style>
